<template>
  <div class="container">
    <!-- 加入开始音效 -->
    <audio controls ref="audioControl" src="../assets/audio/short.wav" hidden="true"
    @canplay="playAudio()"></audio>
    <div class="textBox">
      <p class="restTextAdd">休息倒计时</p>
      <div class="restText">
        {{countDown}}
      </div>
    </div>
  </div>
  
</template>

<script>
export default {
  data() {
    return {
      // 休息倒计时
      countDown: 0
    }
  },
  props: ['timeForRest'],
  mounted() {
    this.countingDown()
  },
  beforeDestroy() {
  },
  methods: {
    countingDown() {
      this.countDown = this.timeForRest
      let timeInter = setInterval(() => {
        this.countDown--
        if (this.countDown === 0) clearInterval(timeInter)
      }, 1000)
      this.clearInter(timeInter, this)
    },
    // 播放ding声音
    playAudio() {
      // 调音量为70%
      this.$refs.audioControl.volume = 0.6
      // 播放
      this.$refs.audioControl.play()
    },
    // 清除定时器函数
    clearInter(timer, that) {
      that.$once('hook:beforeDestroy', () => {
        clearInterval(timer)
        timer = null
      })
    }
  }
}
</script>

<style lang="css" scoped>
.textBox {
  width: 100%;
  text-align: center;
}
/* 休息倒数计时样式 */
.textBox .restTextAdd{
  margin-top: 130px;
  font-size: 16px;
  font-family: "Microsoft YaHei";
  color: #242A33;
}
.textBox .restText {
  margin-top: 80px;
  font-size: 100px;
  font-family: "Arial";
  color: #626C7A;
}

</style>


