<template>
<div class="container">
  <div class="bigBox" v-if="!modal">
    <div class="endInterface" v-show="!startFlag&&endFlag">
      <div class="finishedText">完 成</div>
      <div>
        <Button type="primary" class="startBtn" @click="startSport()">再来一遍</Button>
      </div>
      <div>
        <Button class="startBtn toResetBtn" @click="modal = true">重新设置</Button>
      </div>
    </div>  
    <counting-anime v-if="startFlag"
    :timeForOne="formItem.timeForOne"
    :amountForSports="formItem.amountForSports"
    ></counting-anime>
    <rest-counting v-else-if="!endFlag"
    :timeForRest="formItem.timeForRest">
    </rest-counting>
  </div>
  <div class="bottom">
    <!-- 设置对话框 -->
    <Modal v-model="modal"
    fullscreen
    :closable="false"
    class="mainModal"
    >
    <template slot="header" class="modalHeader">
      <div class="modalTitle">
        <span><Icon type="ios-bicycle" /></span>
        节 奏 设 定
      </div>
      <div class="modalTitleAdd" @click="moreMessage=true">了解更多详情</div>
    </template>
    <Form ref="formItem"
    :model="formItem"
    :rules="ruleValidate"
    label-position="right"
    :label-width="130">
        <FormItem label="动作时长" prop="timeForOne">
          <div class="inputBox">
            <Input v-model.number="formItem.timeForOne" placeholder="单个动作时长"></Input>
            <span>秒</span>
          </div>
        </FormItem>
        <FormItem label="单组个数" prop="amountForSports">
          <div class="inputBox">
            <Input v-model.number="formItem.amountForSports" placeholder="1组含动作个数"></Input>
            <span>个</span>
          </div>
        </FormItem>
        <FormItem label="动作组数" prop="numberOfGroup">
          <div class="inputBox">
            <Input v-model.number="formItem.numberOfGroup" placeholder="动作的组数"></Input>
            <span>个</span>
          </div>
        </FormItem>
        <FormItem label="组间休息" prop="timeForRest">
          <div class="inputBox">
            <Input v-model.number="formItem.timeForRest" placeholder="组间休息时长"></Input>
            <span>秒</span>
          </div>
        </FormItem>
    </Form>
    <template slot="footer" class="theFooter" :style="{top:regLocation}">
      <Button type="primary" class="startBtn" @click="handleSubmit('formItem')">
        开 始 运 动
      </Button>
      <div class="modalFooterAdd">created by Liuanyi</div>
    </template>
    </Modal>
    <!-- 底部重置按钮 -->
    <div class="resetBox" v-show="!modal">
      <Icon type="ios-sync" size="16"/>
      <Button class="reset"  @click.stop="reset()">点击回到设置界面</Button>
    </div>
  </div>
    <!-- 更多详情对话框，包括软件说明、github地址、打赏码 -->
    <Modal v-model="moreMessage" class="moreMessage">
      <template slot="header"><p></p></template>
      <p>这是一个可以帮助你进行运动训练的小工具。你可以设置动作时长、单组个数、组数、休息时间来组织你的训练。运动过程中有提示音。</p>
      <p><span></span></p>
      <p>有意见可以来作者的github反馈：</p>
      <p><a href="#">http://127.0.0.1（待定）</a></p>
      <p>支付宝打赏请扫</p>
      <p><img src="../assets/pic/zhifubao.jpg" alt=""></p>
      <p>谢谢支持!</p>
      <template slot="footer"><span></span></template>
    </Modal>
    <!-- 重新配置确认对话框 。。。abandon-->
    <!-- <div>
      <Modal
      v-model="resetModal"
      class="resetModal"
      :closable="false"
      title="重 新 配 置">
        <template slot="header" class="resetModalHeader">
          <div class="resetModalTitle">
            重 新 配 置
          </div>
        </template>
        <div class="resetModalP">
          <p>确认要重新配置</p>
          <p>并且重新开始运动吗</p>
        </div>
        <template slot="footer" class="resetModalFooter">
          <Button class="resetCancelBtn" @click="resetModal=false">取消</Button>
          <Button class="resetCofirmBtn" @click="modal=true;resetModal=false">确认</Button>
        </template>
      </Modal>
    </div> -->
</div>
</template>

<script>
// 引入运动计时组件和休息计时组件
import CountingAnime from '../components/countingAnime.vue'
import RestCounting from '../components/RestCounting.vue'
// 引入iView使用到的组件，都要在此引入，然后再components中声明组件名称
import { Button, Modal, Icon, Form, FormItem, Input } from 'iview'
// 提前声明本组件用到的三个定时器
var sportTimeOut
var restTimeOut
var bigSetTimeOut

export default {
  data () {
    // 检查"动作时长"、"组间休息时长"参数的函数
    // 确保输入的数据大于0
    const timeOneCheck = (rule, value, callback) => {
      if (value <= 0) {
          return callback(new Error('请确保输入的时长大于0秒'));
      } else {
          return callback();
      }
    }
    // 检查"单组动作个数","动作组数"参数的函数
    // 确保输入的数据大于0且是整数
    const amountSportsCheck = (rule, value, callback) => {
      if (value <= 0) {
        callback(new Error('请确保输入的数字为大于0的整数'));
      } else if (!(Number.isInteger(value))) {
        callback(new Error('请确保输入的数字为大于0的整数'));
      } else {
        callback();
      }
    }

    
    return {
      // 控制对话框的打开和关闭
      modal: true,
      // 控制 CountingAnime 组件的生成，只有当 startFlag 为true时才生成该组件，为false生成RestCounting组件
      startFlag: false, 
      // 当设置的运动结束时，控制完成界面的出现,运动开始时，将其设置为false
      endFlag: false,
      // 存储numberOfGroup的值，并用在计算之中，而formItem中的numberOfGroup保持不变
      savedNumberOfGroup: 0,
      // 控制更多信息对话框的打开和关闭
      moreMessage: false,
      // 动画是否暂停
      // animationPaused: false,
      // 是否弹出确认配置对话框
      // resetModal: false,
      // 对话框表单设置的内容
      formItem: {
        // 动作时长
        timeForOne: 3,
        // 一组包含的动作个数
        amountForSports: 8,
        // 组数
        numberOfGroup: 3,
        // 休息时间
        timeForRest: 10
      },
      // 验证规则
      ruleValidate: {
        // 验证timeForOne
        timeForOne: [
            { required: true, type:'number', message: '请输入动作时长(数字)', trigger: 'blur' },
            { validator: timeOneCheck, trigger: 'blur' }
        ],
        // 验证amountForSports
        amountForSports: [
            { required: true, type:'number', message: '请输入单组动作个数', trigger: 'blur' },
            { validator: amountSportsCheck, trigger: 'blur' }
        ],
        // 验证numberOfGroup
        numberOfGroup: [
            { required: true, type:'number', message: '请输入组数', trigger: 'blur' },
            { validator: amountSportsCheck, trigger: 'blur' }
        ],
        //  验证timeForRest
        timeForRest: [
            { required: true, type:'number', message: '请输入动作时长(数字)', trigger: 'blur' },
            { validator: timeOneCheck, trigger: 'blur' }
        ]
      }
    }
  },
  components: { CountingAnime, RestCounting, Button, Modal, Form, FormItem, Input, Icon },
  watch: {
    modal(newVal, oldVal) {
      if (newVal === true) {
        // 当对话框出现时，将组件销毁
        this.startFlag = false
      }
    },

  },
  mounted() {
    try {
        // 根据屏幕高度设置 top 值
        this.regLocation = res.windowHeight - 113 + 'px' // 此处的 118 是我的按钮离底部的距离加上按钮的高度
      } catch (e) {
        // error
        uni.$u.toast(e)
      }
  },
  methods: {
    startSport() {
      // console.log("startSport")
      // 设置savedNumberOfGroup的值
      this.savedNumberOfGroup = this.formItem.numberOfGroup
      // 点击“开始运动后”，关闭设置对话框
      this.modal = false
      // 存储当前的this
      let that = this
      // nextCircle用于将一次大循环结束后继续下一个大循环
      function nextCircle() {
        // 计算单组运动的时间，包括开始倒计时(3s)，开始(1s)，结束(1s),每组动作结束时停(1s)单位为毫秒
        let timeForSport = ( 1 + 3 + that.formItem.timeForOne * that.formItem.amountForSports + 2 ) * 1000
        // 计算休息时间
        // 还是要用到定时器，哈哈哈
        let restTime = that.formItem.timeForRest * 1000
        // 循环一组动作
        that.oneCircle()
        // 结束一组动作后,开始下一组
        bigSetTimeOut = setTimeout(() => {
          bigIterator.next()
        }, timeForSport+restTime)
      }
      // 大生成器函数
      function * bigGen() {
        // 运动开始后，隐藏对话框
        that.endFlag = false
        // 当还有运动组数savedNumberOfGroup还未完成时开始运动,否则便停止
        while(that.savedNumberOfGroup > 0) {
          // console.log(that.savedNumberOfGroup)
          // 开始下一组
          yield nextCircle()
          // 开始后savedNumberOfGroup减一
          that.savedNumberOfGroup --
        }
        // 运动结束，使完成界面显示
        that.endFlag = true
      }
      // 设置生成器并且启动
      var bigIterator = bigGen()
      bigIterator.next()
    },
    oneCircle() {
      // 计算休息时间
      let restTime = this.formItem.timeForRest * 1000
      // 计算单组运动的时间，包括开始倒计时(3s)，开始(1s)，结束(1s),每组动作结束时停(1s)单位为毫秒
      let timeForSport = ( 1 + 3 + this.formItem.timeForOne * this.formItem.amountForSports + 2 ) * 1000
      // 等到计时器第一次计时完成后才开始执行函数
      // 在vue的自定义函数中使用this指向本组件，定义函数时要使用箭头函数
      // 控制先进行运动时段然后再进行休息
      let testThat = this
      // 1.控制运动时段准时结束
      function sportTime() {
        // 开始运动
        testThat.startFlag = true
        // 打印当前参数
        // console.log("运动次数"+ testThat.amountForSports)
        // 设置停止时间
        sportTimeOut = setTimeout(() => {
          // numOfGroup--
          testIterator.next()
        }, timeForSport)
      }
      // 2.控制休息时间
      function restTimeControl() {
        // 开始休息
        testThat.startFlag = false
        // 设置停止时间
        restTimeOut = setTimeout(() => {
          // bigIterator.next()
          testIterator.next()
        }, restTime)
      }
      // 设置生成器函数,实现一个运动-休息循环
      function * testGen() {
        yield sportTime()
        yield restTimeControl()
      }
      // 设置生成器并且启动
        var testIterator = testGen()
        testIterator.next()

    },
    reset() {
      // 直接打开设置界面
      this.modal = true
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
          if (valid) {
              // 表单验证成功继续程序
              if(sportTimeOut) {
                clearTimeout(sportTimeOut)
              }
              if(restTimeOut) {
                clearTimeout(restTimeOut)
              }
              if(bigSetTimeOut) {
                clearTimeout(bigSetTimeOut)
              }
              this.startSport()
          } else {
            // 验证失败
            console.log("验证失败")
          }
      })
    }
  }
}
</script>

<style lang="css" scoped>
.container {
  margin: 0 auto;
  background-color: #fff;
}
.bigBox {
  position:relative;
  text-align: center;
}
.bigBox .endInterface{
  width: 100%;
}
.bottom {
  width: 100%;
  text-align: center;
}
/*  首页对话框  */
/*  首界面-标题部分 */
.modalHeader {
  width: 100%;
}
.modalTitle {
  margin-top: 56px;
  color: #2568CD;
  font-size: 30px;
  font-weight: 700;
  font-family: 'Microsoft YaHei';
  text-align: center;
}
.modalTitleAdd {
  margin-top: 12px;
  color: #A0A1A3;
  font-size: 16px;
  font-weight: 100;
  font-family: 'Microsoft YaHei';
  text-align: center;
}
/* 对话框中间单位样式设置 */
.inputBox span {
  font-size: 20px;
  font-family: "Microsoft YaHei";
  color: #3D434D;
  margin-left: 24px;
}
/* “开始运动”按钮设置 */
.theFooter {
  position: fixed;
}
.startBtn {
  width: 296px;
  height: 65px;
  font-size: 24px;
  font-family: "Microsoft YaHei";
  color: #FCFDFF;
  background-color: #2568CD;
  box-shadow: 0px 4px 6px rgba(102, 112, 128, 0.2);
}
/* 设置"开始运动"按钮下的说明文字样式 */
.modalFooterAdd {
  margin-top: 12px;
  color: #A0A1A3;
  font-size: 16px;
  font-weight: 100;
  font-family: 'Microsoft YaHei';
  text-align: center;}
/* 底部"重新配置"按钮样式 */
.resetBox {
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 60px;
  text-align: center;
}
.reset {
  vertical-align: baseline;
  padding-left: 0px;
  font-size: 16px;
  font-weight: 100;
  font-family: "Microsoft YaHei";
  color: #3D434D;
  border: 0px solid #999
}
/* "重新设置"对话框设计 */
/* .resetModalHeader {
  width: 100%;
}
.resetModalTitle {
  margin: 15px auto;
  color: #fff;
  font-size: 28px;
  font-family: 'Microsoft YaHei';
  text-align: center;
  background-color: #2568CD;
}
.resetModalP {
  margin: 29px auto 38px;
  font-size: 20px;
  font-family: 'Microsoft YaHei';
  line-height: 36px;
  color: #3D434D;
  text-align: center;
}
.resetModalFooter {
  width: 100%;
}
.resetModalFooter>.resetCofirmBtn {
  background-color: #2568CD !important;
} */
/* "完成"画面样式 */
.endInterface .finishedText {
  margin-top: 140px;
  margin-bottom: 60px;
  font-size: 50px;
  font-family: "Microsoft YaHei";
  color: #2568CD;
}
.endInterface .toResetBtn {
  margin-top: 20px;
  background-color: #91BDFF;
}
/* 更多详情对话框样式 */
.moreMessage p {
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 28px;
  font-family: "Microsoft YaHei";
  color: #3D434D;
}
.moreMessage p img {
  margin: 0 auto;
  width: 60%;
}
</style>



