<template>
  <div class="container">
    <!-- 加入开始音效 -->
    <audio controls ref="audioControl" src="../assets/audio/long.wav" hidden="true"
    @canplay="playAudio()"></audio>
    <!-- 圆圈动画 -->
    <div class="bigCircle" ref="timeSet" v-show="timeFlag == 3">
    </div>
    <!-- 界面切换 -->
    <div class="textBox">
      <!-- 3，2，1倒数界面 -->
      <div class="countDownFrame" v-if="timeFlag == 1">
        <p class="countDownTextAdd">运动开始倒计时</p>
        <div class="countDownNum">{{countNum}}</div>
      </div>
      <!-- “开始”界面 -->
      <div class="startFrame" v-else-if="timeFlag == 2">
        <div class="startText">开 始</div>
        <div class="startIcon">
          <Icon type="ios-bicycle" size="20"/>
        </div>
      </div>
      <!-- 动作过程界面 -->
      <div class="sportFrame" v-else-if="timeFlag == 3">
        <!-- 加入计数音效 -->
        <audio controls ref="shortAudio" src="../assets/audio/muyu.wav" hidden="true"
        @canplay="playShortAudio()"></audio>
        <p class="sportTextAdd">已 做</p>
        <div class="sportCountTimes">{{countTimes}}</div>
      </div>
      <!-- 动作结束界面 -->
      <div class="endFrame" v-else>
        <div class="endText">休 息</div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon, Button } from 'iview'
export default {
  data () {
    return {
      // timeFlag 1代表进入运动开始倒计时，2代表进入“开始运动”界面，3代表运动过程中，4代表“结束运动”界面
      timeFlag: 2,
      // 开始倒计时
      countNum: 99,
      // 动作个数计数
      countTimes: 0
    }
  },
  components: { Icon, Button },
  props: [ 'timeForOne', 'amountForSports' ],
  watch: {
    countTimes (newVal, oldVal) {
      if (this.timeFlag === 3) {
        if (newVal > oldVal) {
          this.$refs.shortAudio.play()
        }
      }
    }
  },
  mounted () {
    this.theGenerator()
  },
  beforeDestroy () {
  },
  methods: {
    // 采用生成器函数来实现该组件的功能
    theGenerator () {
      // 控制圆圈的动画时长和次数
      // 在mounted中才能读取到this.$refs中的对象，在created中运行该函数无法读取this.$refs中的对象
      this.$refs.timeSet.style.animationDuration = this.timeForOne / 2 + 's' 
      this.$refs.timeSet.style.animationIterationCount = this.amountForSports*2
      // 保存当前的this到that中
      let that = this
    // 1.展示“开始运动”界面
      function showBegin () {
        // 让界面显示“开始运动”
        that.timeFlag = 2
        // 让该界面停留1秒钟
        const timeOut1 = setTimeout(() => {
          iterator.next()
        }, 1000)
        // 组件销毁时定时器也被销毁
        that.clearOut(timeOut1, that)
      }
      // 2.展示“3，2，1”倒数界面
      function showBeforeStart () {
        // 展示“3，2，1”倒数界面
        that.timeFlag = 1
        // 将倒数计数初始值设置为 3
        that.countNum = 3
        const countNumDown = setInterval( () => {
          that.countNum --
          if (that.countNum === 0) {
            clearInterval(countNumDown)
            iterator.next()
          }
        }, 1000)
        // 组件销毁时定时器也被销毁
        that.clearInter(countNumDown, that)
      }
      // 3.展示运动中界面
      function showSporting () {
        // 展示运动中界面
        that.timeFlag = 3
        // 将运动中的个数计数初始值设置为0
        that.countTimes = 0
        // 保存单次运动要求的动作个数
        let amount = that.amountForSports
        let timeForOne = that.timeForOne
        // 设置定时器开始给动作计数
        const interval = setInterval(() => {
        // 动作次数+1
        that.countTimes++
        // 要求动作的数量-1
        amount--
        // debugger
        // 动作数量达标时，清除计时器，执行下一步
        if(amount === 0) {
          clearInterval(interval)
          setTimeout(() => {
            iterator.next()
          }, 1000)
        }
        }, timeForOne * 1000)
        that.clearInter(interval, that)
      }
      // 4.展示“休息时间”（“结束运动”）界面
      function showEnding() {
        // 展示“休息时间”（“结束运动”）界面
        that.timeFlag = 4
        // 让该界面停留1秒钟
        const timeOut2 = setTimeout(() => {
        // 继续下一步
        iterator.next()
        }, 1000);
        // 组件销毁时定时器也被销毁
        that.clearOut(timeOut2, that)
      }
  
  // 生成器函数
  function * gen () {
    // 开始界面
    yield showBegin()
    // 倒数界面
    yield showBeforeStart()
    // 运动计数界面
    yield showSporting()
    // 结束休息界面
    yield showEnding()
  }
  var iterator = gen()
  iterator.next()
    },
    // 播放ding声音
    playAudio() {
      // 调音量为40%
      this.$refs.audioControl.volume = 0.4
      // 播放
      this.$refs.audioControl.play()
    },
    playShortAudio() {
      if (this.countTimes !== 0) {
      // 调音量为50%
        this.$refs.shortAudio.volume = 0.5
        this.$refs.shortAudio.play()
      }
    },
    // 清除周期定时器函数
    clearInter(timer, that) {
      that.$once('hook:beforeDestroy', () => {
        clearInterval(timer)
        timer = null
      })
    },
    // 清除延时定时器函数
    clearOut(timer, that) {
      that.$once('hook:beforeDestroy', () => {
        clearTimeout(timer)
        timer = null
      })
    }
  }
}
</script>
<style lang="css" scoped>
.container {
  position: relative;
  width: 100%;
}
/* 动画样式 */
@keyframes changing {
  0% {
    transform: scale(0.2);
  }
  15% {
    transform: scale(0.2);
  }
  85% {
					transform: scale(1);
				}
  100% {
          transform: scale(1);
  }
}
/* 动画圆圈样式 */
.bigCircle {
  position: absolute;
  top: 166px;
  left: 50%;
  margin-left: -150px;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  animation-name: changing;
  animation-duration: 4s;
  /* animation-delay: 4s; */
  animation-direction: alternate;
  animation-iteration-count: 2;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  background-color: #91BDFF;
}
.textBox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
}

/* "开始"界面样式 */
.startFrame .startText {
  margin-top: 240px;
  font-size: 50px;
  font-family: "Microsoft YaHei";
  color: #2568CD;
}
.startFrame  .startIcon {
  margin-top: 32px;
  color: #2568CD;
}
/* 3，2，1倒计时界面样式 */
.countDownFrame .countDownTextAdd {
  margin-top: 130px;
  font-size: 16px;
  font-family: "Microsoft YaHei";
  color: #242A33;
}
.countDownFrame .countDownNum {
  margin-top: 80px;
  font-size: 100px;
  font-family: "Arial";
  color: #2568CD;
}
/* 运动计数界面样式 */
.sportFrame .sportTextAdd {
  margin-top: 130px;
  font-size: 16px;
  font-family: "Microsoft YaHei";
  color: #A0A1A3;
}
.sportFrame .sportCountTimes {
  margin-top: 117px;
  font-size: 60px;
  font-weight: 700;
  font-family: "Arial";
  color: #FAFBFC;
  text-shadow: rgba(0, 0, 0, 0.06);
}
/* 休息界面样式 */
.endFrame .endText {
  margin-top: 240px;
  font-size: 50px;
  font-family: "Microsoft YaHei";
  color: #2568CD;
}
</style>



